import GxLoader from '@components/_Core/Utils/GxLoader'
// import GxCardModal from '@src/components/_Core/Cards/GxCardModal'

import { GET_LEDGER_ACCOUNTS } from '@src/apollo/queries'

export default {
  name: 'LedgerAccounts',
  data() {
    return {
      accountingFirmNumber: null,
      ledgerAccounts: null,
      levels: null,
      loading: true,
      clientNumber: localStorage.getItem('clientNumber'),
      beginningPeriod: null,
      endingPeriod: null,
    }
  },
  components: {
    GxLedgerAccounts,
    GxLoader,
  },
  apollo: {
    getLedgerAccounts: {
      query: GET_LEDGER_ACCOUNTS,
      variables() {
        return {
          clientNumber: [localStorage.getItem('clientNumber')],
        }
      },
      update(data) {
        this.accountingFirmNumber =
          data.myAccountingFirms[0].accountingFirmNumber
        this.ledgerAccounts =
          data.myAccountingFirms[0].clients[0].accounting.chartOfAccounts.ledgerAccounts
        this.levels =
          data.myAccountingFirms[0].clients[0].accounting.chartOfAccounts.levels
        this.beginningPeriod =
          data.myAccountingFirms[0].clients[0].accounting.beginningPeriod
        this.endingPeriod =
          data.myAccountingFirms[0].clients[0].accounting.endingPeriod
      },
      fetchPolicy: 'no-cache',
    },
  },
  watch: {},
  computed: {},
  created() {},
  methods: {
    downloadExcel() {},
    finishRender() {
      this.loading = false
    },
  },
}
</script>
